<template>
<section>
  <!-- 病例列表 -->
  <el-row>
    <el-col :span="24" class="case">
      <div class="case-item" v-for="value in listData" :key="value.registrationNum">
        <div class="case-item-topbar"></div>
        <div class="case-item-content">
          <span>就诊日期</span>: {{value.onSetDate}}
        </div>
        <div class="case-item-content">
          <span>就诊号</span>: {{value.registrationNum}}
        </div>
        <div class="case-item-content">
          <span>科室</span>: {{value.deptName}}
        </div>
        <div class="case-item-content">
          <span>诊治医生</span>: {{value.userName}}
        </div>
        <div class="case-item-link">
          <div>复制病例</div><div><router-link :to="'case_info?id='+ value.registrationNum + '&mn=' + value.mecidalNum ">查看详情</router-link></div>
        </div>
      </div>
    </el-col>
  </el-row>
</section>
</template>
<script>
import {getEmpBasicList} from '../api'
export default {
  props:['num'],
  data(){
    return{
      listData: [], // 列表数据
    }
  },
  methods:{
    handelLinkTo(){

    }
  },
  mounted(){
    getEmpBasicList({mecidalNum: this.num}).then(res=>{
      if(res.data.code == '0'){
        this.listData = res.data.data
      }
    })
  }
}
</script>
<style>
.case{
  display: flex;
  flex-wrap:wrap;
}
.case .case-item{
  margin: 24px;
  width: 280px;
  border-radius: 4px;
  overflow: hidden;
  background-color: #F7F0EF;
}
.case .case-item .case-item-topbar{
  height: 17px;
  width: 100%;
  background-color: #BF7045;
}
.case .case-item .case-item-content{
  margin-top: 24px;
  padding-left: 32px;
  color: #333;
}
.case .case-item .case-item-content span{
  display: inline-block;
  width: 70px;
  text-align: right;
  color: #666;
}
.case .case-item .case-item-link{
  display: flex;
  justify-content: center;
  margin: 32px 0;
}
.case .case-item .case-item-link div,.case .case-item .case-item-link a{
  margin: 0 16px;
  color: #3E85FA;
  cursor: pointer;
}
</style>





