  saveOrFinishVisit1, // 直接完成就诊
  getWesternDrugByFangJiSubID, // 获取西药方剂详情
} from '../../api'
import { mapState,mapGetters,mapMutations} from 'vuex'
import item_select from '../../components/Check_item_select.vue' // 检查项目选择
import case_list from '../../components/case_list.vue' // 病例
export default {
  components:{
    'item-select': item_select,
    'case-list': case_list
  },
  computed:{
    ...mapGetters({
      getDrugWay: "common/getDrugWay",
    //  getFrequency: "common/getFrequency",
    }),
    isAnorectal:function(){ // 肛肠科 判断用
      if(this.$route.path.includes('anorectal')){
        return true
      }else{
        return false
      }
    },
    // pushCheckData: function(){

    // },
    ...mapState('common',{
      frequencyList: state => state.frequency,
    }),
    ...mapState('patient',{
      mecidalNum: state => state.mecidalNum,
      registrationNum:state=>state.registrationNum,
      empBasicMainID:state=>state.empBasicMainID,
      deptCode:state=>state.deptCode
    }),
    titleList: function (){
      return this.xiyaoTitleList
    },
    linzhengTitle: function (){
      return this.linzhengTitleList
    },
    linzhengCompData:{
      get:function () {
        let arrData = []
        this.linzhengTitleList.forEach(element=>{
          if (element.id == this.linzhengActiveId) {
            arrData = element.data
          }
        })
        return arrData
      },
      set:function () {
        return this.linzhengCompData
      }
    },
    xiyaoListData:{
      get:function () {
        let arrData = []
        this.xiyaoTitleList.forEach(element=>{
          if (element.id == this.xiyaoActiveId) {
            arrData = element.data
          }
        })
        return arrData
      },
      set:function () {
        return this.xiyaoListData
      }
    },
  },
  watch:{
    // linzhengTitleList:{
    //   handler(value){
    //     return
    //     let txt = ''
    //     value.forEach(element=>{
    //       element.data.forEach(item=>{
    //         txt += item.detailItemName + ' '
    //       })
    //       txt += ' ; '
    //     })
    //     this.linZhengShowText = txt
    //   },
    //   deep:true
    // },
    // xiyaoTitleList:{
    //   handler(value){
    //     return
    //     let txt = ''
    //     value.forEach(element=>{
    //       element.data.forEach(item=>{
    //         txt += item.drugName + ' '
    //       })
    //       if(!txt) return
    //       txt += ' ; '
    //     })
    //     this.xiyaoShowText = txt
    //   },
    //   deep:true
    // },
    // empBasicMainID(){
    //   if(this.empBasicMainID){
    //     this.getSavedData()
    //     this.handelGetEmpBasic()
    //     this.handelGetXYList()
    //     this.getLZZL()
    //   }
    // }
