        drugWayCodeValue: '', // 用药方式
        frequencyCodeValue: '', // 用药频率
        useDaysValue: '', // 用药天数
        totalAmountValue: '', // 总量
        quanitityValue: '', // 领量
        lPriceValue: '', // 单价
        signDrugWay: '',// 标记 用药方式搜索
        wholeMoneyValue: '', // 金额
      }
    }
  },
  methods:{
    ...mapMutations('common',['getFrequency']),
    tplFilter(data){ //       || data.usedPhrasesContent.includes(this.searchTpl)
      if(!this.searchTpl || data.usedPhrasesName.includes(this.searchTpl)){
        return true
      }else{
        return false
      }
    },
    handelLinZhengList(){ // 临症列表
      this.dialogLinZhengAdd = true
      this.getLinZhengListData()
    },
    handelSaveLinZhengData(){ // 保存临症数据
      if(this.linzhengTitleList < 1){
        this.$message.error('请选择添加处方!')
        return
      }
      if(this.linzhengTitleList[0] === undefined || this.linzhengTitleList[0].data.length<1){
        this.$message.error('请选择添加处方项目!')
        return
      }
      let list = []
      this.linzhengTitleList.forEach(element=>{
        if(element.data === undefined || element.data.length === 0){
          this.$message.error('请选择添加处方项目!')
          return
        }
        list.push({
          empBasicMainID: this.empBasicMainID,
          getDetailList: element.data
        })
      })
      saveWesternCure(list).then(res=>{
          if (res.data.code === '0') {
            this.$message.success('保存成功')
            let txt = ''
            list.forEach(element=>{
              element.getDetailList.forEach(item=>{
                txt += item.detailItemName + ' '
              })
              txt += ';'
            })
            this.linZhengShowText = txt
            this.dialogLinZheng = false
          }else{
            this.$message.error('保存失败')
          }
      })
    },
    getLinZhengListData(){
      getWesternCureList({searchText: this.linzhengSearchText}).then(res=>{
        this.linzhengList=res.data.data
      })
    },
    handelDbSelectLZ(row){ // 双击添加临症项目
      let exist = false
      this.linzhengCompData.find(element=>{
        if(element.detailItemCode == row.detailItemCode){
          exist = true
        }
      })
      if (exist) {
        this.$message.error('项目已存在列表中!')
        return
      }
      for (let i = 0; i < this.linzhengTitleList.length; i++) {
        if(this.linzhengTitleList[i].id === this.linzhengActiveId){
          getWesternCureDetailList({detailItemCode: row.detailItemCode}).then(res=>{
            if(res.data.code=='0'){
              row.price = res.data.data[0].price
              this.linzhengTitleList[i].data.push(row)
              this.linzhengTitleList = [...this.linzhengTitleList]
              this.$message.success('项目添加成功!')
            }else{
              this.$message.warning('获取项目详情失败!')
            }
          })
        }
      }
    },
    pagePrint(){ // 打印病例
      window.open('#/printPage?id=' + this.registrationNum,'_blank')
    },
    handelxybm(){ // 西医病名
      this.dialog_xiyao = true
      this.xybmText = ''
      this.handelSearchXY()
    },
    handelSearchXY(){ // 查询西医病名
      getAllWesternMedicineName({searchText: this.searchXY}).then(res=>{
        this.xyData = res.data.data
      })
    },
    handleFinish(){ // 直接完成就诊
      this.handelSaveEmpBasic() // 自动保存
      saveOrFinishVisit1({
        registrationNum: sessionStorage.getItem('current_registrationNum')
      }).then(res=>{
        if(res.data.code=='0'){
          this.$message.success('就诊已完成!')
          this.$router.push('list')
        }else{
          this.$message.error('保存失败!')
        }
      })
    },
    // 打印页面
    handlePrint (type) {
      let toPath = '/print/western'
      if( type == 'linzheng' ) {
        toPath = '/print/linzheng'
      }
      let routeData = this.$router.resolve({ path: toPath, query: {  id: this.registrationNum } })
      window.open(routeData.href, '_blank')
    },
    handelSelectXiYi(row){ // 选择西医病名
      this.xybmText = row.westernMedicineName
      sessionStorage.setItem('zhzy_gcxt_xybm_name',row.westernMedicineName)
      sessionStorage.setItem('zhzy_gcxt_xybm_code',row.westernMedicineCode)
      this.dialog_xiyao = false
    },
    handelSaveEmpBasic(){ // 保存病例信息
      let getContent = function (id) {
        return document.getElementById(id).innerText
      }
      let empBasic = {}
      empBasic.empBasicMainID = this.empBasicMainID
      empBasic.chiefComplaint = getContent('text-40') // 主诉
      empBasic.physicalExamination = getContent('text-50') // 体格检查
      empBasic.historyPresent = getContent('text-10') // 现病史
      empBasic.historyPast = getContent('text-30') // 既往史
      empBasic.historyAllergic = getContent('text-20') // 过敏史
      updateEmpBasic(empBasic).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('保存成功!')
        }else{
          this.$message.error('保存失败!')
        }
      })
    },
    handelGetEmpBasic(){ // 获取主诉等信息
      let setContent = function (id,value) {
        if(!value){
          value = ''
        }
        document.getElementById(id).innerText = value
      }
      getEmpBasicMainEntyTable({registrationNum: this.registrationNum}).then(res=>{
        if(res.data.code==='0'){
          let info = res.data.data[0]
          setContent('text-40',info.chiefComplaint)
          setContent('text-50',info.physicalExamination)
          setContent('text-10',info.historyPresent)
          setContent('text-30',info.historyPast)
          setContent('text-20',info.historyAllergic)
        }
      })
    },
    handelSelect(e){ // 切换左侧标签
      if(this.activeTagIndex==e.target.dataset.index){
        return
      }
      document.querySelector('.bingqing-active').className=''
      e.target.className = "bingqing-active"
      this.activeTagIndex = e.target.dataset.index
      this.searchTpl = '' // 清除模板过滤
      this.getTpl()
    },
    handelAddText(text){ // 通过模板添加记录
      text = text + " "
      document.querySelector('#text-' + this.activeTagIndex).textContent += text
    },
    handleCheck(n){ // 弹窗 检测
      if(n==0){
        this.dialog_check0 = true;
      }else{
        this.dialog_check1 = true;
      }
    },
    getDataCheck(data){ // 检查返回值
      if(data.flag == 0){
        this.jianceText = data.content + " "
      }else{
        this.jianyanText= data.content + " "
      }
    },
    handleClose(){}, //
      handleNodeClick(data){ // 弹窗内 检测 左侧选择
    },
    handelSelectTab(e){ // 上侧切换标签
      document.querySelector('.top-select-active').className=''
      e.target.className = "top-select-active"
      this.activeTag = e.target.dataset.index
    },
    getTpl(){ // 获取病状模板
      let current_deptCode = '10001'
      if(sessionStorage.getItem('current_deptCode')=='10004'){
        current_deptCode = '10004'
      }
      getModelTable({
        usedPhrasesType: this.activeTagIndex,
        deptCode: current_deptCode,
        searchText: this.searchText
        }).then(res=>{
          if(res.data.code == '0'){
            this.tplData = res.data.data
          }else{
            this.tplData = []
          }
      })
    },
    handelLink(){ // 链接跳转
      if(this.isAnorectal && !this.xybmText){
        this.$message.error('请先选择西医病名！')
        return
      }
      this.handelSaveEmpBasic() // 自动保存
      if(this.activeTag=='1'){
        this.$router.push('selectSymptom')
      }
      if(this.activeTag=='2'){
        this.$router.push('selectTreatment')
      }
      if(this.activeTag=='3'){
        this.$router.push('immediate')
      }
    },
    handelAddChuFang(){ // 新添加处方
      let length = this.xiyaoTitleList.length
      if(length==0){
        GetFangJiCode().then(res=>{
          this.xiyaoTitleList.push({id:1,data:[],operaType:0,fangJiSubCode: res.data.data.getBillCode})
        })
        return
      }
      let last = this.xiyaoTitleList[length-1]
      GetFangJiCode().then(res=>{
        this.xiyaoTitleList.push({id:last.id+1,data:[],operaType:0,fangJiSubCode: res.data.data.getBillCode})
      })
    },
    handelAddChuZhi(){ // 新添加处治
      let length = this.linzhengTitleList.length || 0
      if(length==0){
       this.linzhengTitleList.push({id:1,data:[]})
        return
      }
      let last = this.linzhengTitleList[length-1] || 0
      this.linzhengTitleList.push({id:last.id+1,data:[]})
    },
    handleLinZhengItemDelete(index,row){ // 删除处治项目
      this.linzhengCompData.splice(index,1)
    },
    handelDelLinZheng(item){ // 删除处治
      this.linzhengTitleList.splice(this.linzhengTitleList.indexOf(item),1)
    },
    handelDelTitle(item){ // 删除处方
      if(item.operaType == '1'){ // 修改 删除远程
        deleteWesternDrugFangji({empBasicMainID: this.empBasicMainID,fangJiSubID: item.fangJiSubID}).then(res=>{
          if(res.data.code ==='1'){
            this.$message.success('删除成功!')
            this.xiyaoTitleList.splice(this.xiyaoTitleList.indexOf(item),1)
          }else{
            this.$message.error('删除失败!')
          }
        })
      }else{ // 新增 可直接删除
        this.xiyaoTitleList.splice(this.xiyaoTitleList.indexOf(item),1)
      }
    },
    handelActiveLinZheng(e,t){ // 激活/选中处治
      this.linzhengActiveId = e.id
      if (document.querySelector(".title-linzheng-list .active")) {
        document.querySelector(".title-linzheng-list .active").className = ''
      }
      t.target.className = 'active'
    },
    handelActive(e,t){ // 激活/选中处方
      this.xiyaoActiveId = e.id
      if (document.querySelector(".title-list .active")) {
        document.querySelector(".title-list .active").className = ''
      }
      t.target.className = 'active'
    },
    handelXiyaoList(){ // 打开西药列表
      this.dialogXiYaoSelect = true
      this.tableArgs.searchText = ''
      this.tableArgs.pageNumber = 1
      this.handelGetXiYaoTable()
    },
    handelGetXYList(){ // 获取西药处方
      let index = 0
      getFangJiTable({empBasicMainID: this.empBasicMainID}).then(res=>{
        if(res.data.code==='1') {
          this.handelAddChuFang()
          return
        }
        res.data.data.forEach(element=>{
          this.xiyaoTitleList[index]={
            id: index + 1,
            data: [],
            operaType: 1, // 修改
            fangJiSubID: element.fangJiSubID,
            fangJiSubCode: element.fangJiSubCode
          }
        this.handelGetWesternDrugByFangJiSubID(index,element.fangJiSubID)
        index ++
        })
      })
    },
    funSetXYOperaType(){ // 西药保存完成 修改本地状态
      this.xiyaoTitleList.forEach(element=>{
        element.operaType = 1
      })
    },
    handelGetWesternDrugByFangJiSubID(index,id){ // 获取西药方剂详情
       getWesternDrugByFangJiSubID({
         empBasicMainID: this.empBasicMainID,
            fangJiSubID: id
        }).then(res=>{
         if(res.data.code === '0'){
           let txt = ''
           res.data.data.forEach((element)=>{
             txt += element.drugName + '  '
             this.xiyaoTitleList[index].data.push({
                drugCode: element.drugCode,
                drugName: element.drugName, // 编号
                frequencyCode: element.frequencyCode, // 用药频率
                drugWayCode: element.drugWayCode, // 用法
                packageUnits: element.packageUnits, // 规格
                measurementCoefficient: element.measurementCoefficient, // 单个(含量)
                prescriptionUnitName: element.prescriptionUnitName,
                consumption: element.singleMeasure, // 用量
                singleMeasureUnitName: element.singleMeasureUnitName, // 单位(单个)
                totalAmountUnitName: element.totalAmountUnitName, // 单位(总)
                prescriptionUnitName: element.prescriptionUnitName, // 处方单位
                useDays: element.useDays, // 天数
                totalAmount: '0', // 总量
                quanitity: 0, // 领量
                lprice: element.lprice, // 单价
                wholeMoney: '0.00', // 金额
              })
           })
           this.xiyaoTitleList[index].data.forEach(element=>{
             this.computeRowdata(0,element)
           })
          this.xiyaoTitleList = [...this.xiyaoTitleList]
          this.xiyaoShowText += txt + ';'
         }else{
           this.xiyaoTitleList[index].data = []
         }
       })
    },
    handelSaveXiyiChufang(){ // 西药处方保存
      let postData = []
      let allowable = true
      this.xiyaoTitleList.forEach(element=>{
        if(element.data.length>0){
          element.data.forEach(element => {
            if(!element.useDays || !element.quanitity || !element.frequencyCode){
              allowable = false
            }
          });
          postData.push({
            empBasicMainID: this.empBasicMainID,
            fangJiSubID: element.fangJiSubID || 0,
            fangJiSubCode: element.fangJiSubCode,
            fangJiSubName: '',
            operaType: element.operaType,
            fangJiDetailSubList: element.data,
          })
        }else{
          allowable = false
        }
      })
      if(!allowable || postData.length==0){
        this.$message.error('请将数据信息填写完整!')
        return
      }
      saveWesternDrugFangji(postData).then(res=>{
        if(res.data.code === '1'){
          this.$message.success('西药处方保存成功')
          this.dialogXiYao = false
          this.funSetXYOperaType()
          let txt = ''
          postData.forEach(element=>{
            element.fangJiDetailSubList.forEach(item=>{
              txt += item.drugName + ' '
            })
            txt += ';'
          })
          this.xiyaoShowText = txt
        }else{
          this.$message.success('西药处方保存失败')
        }
      })
    },
    handleChuFangItemDelete(index,row){ // 西药列表项目删除
      this.xiyaoListData.splice(index,1)
    },
    handelGetXiYaoTable(){ // 获取西药列表
      // if(this.xiyaoList.length>0 && this.xiyaoSearchTxt==''){
      //   return
      // }
      getXiYaoTable(this.tableArgs).then(res=>{
        if(res.data.code =='0'){
          this.xiyaoList = res.data.data.content
          this.total = res.data.data.total
        }else{
          this.xiyaoList = []
          this.total = 0
        }
      })
    },
    handelDbSelectItem(row){ // 双击选中
      let exist = false
      this.xiyaoListData.find(element=>{
        if(element.drugCode == row.drugCode){
          exist = true
        }
      })
      if (exist) {
        this.$message.error('药品已存在列表中!')
        return
      }
      this.xiyaoItemObj.drugName = row.drugName
      this.xiyaoItemObj.drugCode = row.drugCode
      for (let i = 0; i < this.xiyaoTitleList.length; i++) {
        if (this.xiyaoTitleList[i].id === this.xiyaoActiveId) {
          getWesternMedicine({searchText: row.drugCode}).then(res=>{
            if(res.data.code=='0'){
              let obj = res.data.data.content[0]
              this.xiyaoTitleList[i].data.push({
                drugCode: obj.drugCode,
                drugName: obj.drugName, // 编号
                frequencyCode: '', // 用药频率
                drugWayCode: obj.drugWayCode, // 用法
                packageUnits: obj.packageUnits, // 规格
                measurementCoefficient: obj.measurementCoefficient, // 单个(含量)
                prescriptionUnitName: obj.prescriptionUnitName,
                consumption: obj.singleMeasure, // 用量
                singleMeasureUnitName: obj.singleMeasureUnitName, // 单位(单个)
                totalAmountUnitName: obj.totalAmountUnitName, // 单位(总)
                prescriptionUnitName: obj.prescriptionUnitName, // 处方单位
                useDays: obj.useDays, // 天数
                totalAmount: '0', // 总量
                quanitity: 0, // 领量
                lprice: obj.lprice, // 单价
                wholeMoney: '0.00', // 金额
              })
            }
          })
        }
      }
      this.dialogXiYaoSelect = false
    },
    computeRowdata(index,row){ // 处方联动计算
      let current = {coefficient: 0}
      if(row.frequencyCode){
        current = this.frequencyList.find(element=>{
          return element.frequencyCode == row.frequencyCode
        })
      }
      row.quanitity = Math.ceil((row.useDays * current.coefficient * row.consumption) / row.measurementCoefficient) // 领量
      row.totalAmount =  current.coefficient * row.consumption * row.useDays // 总量
      row.wholeMoney = ((row.lprice * row.quanitity * 100)/ 100).toFixed(2) // 金额
    },
    handlePageChange(index){ // 页码切换
      this.tableArgs.pageNumber = index
      this.handelGetXiYaoTable()
    },
    handelXiYao(){ // 西药处方窗口
      this.dialogXiYao = true
    },
    handelZhiLiao(){
      this.dialogLinZheng = true
    },
    getSavedData(){ // 获取初始信息
      // 获取检查检验信息
      getYiCunCheckItemList({
        empBasicMainID: this.empBasicMainID,
      }).then(res=>{
        if(res.data.code==='0'){
          this.pushCheckData = res.data.data
          res.data.data.forEach(element=>{
            if(element.checkFlag===0){
              this.jianceExist[0].data += element.detailItemName + '   '
            }else{
              this.jianceExist[1].data += element.detailItemName + '   '
            }
          })
          this.jianyanText = this.jianceExist[1].data
          this.jianceText = this.jianceExist[0].data
        }
      })
    },
    getFangJiCode(){ // 获取方剂code
      GetFangJiCode().then(res=>{
        this.xiyaoTitleList[0].code = res.data.data.getBillCode
      })
    },
    getLZZL(){ // 患者已存的临床治疗
       getYiCunMedicareCure({empBasicMainID: this.empBasicMainID}).then(res=>{
         if(res.data.code == '1'){
           this.linzhengTitleList[0] = {id:1,data:[]}
           return
         }
         if(res.data.data === null || res.data.data.length == 0){
           res.data.data = []
           this.linzhengTitleList[0] = {id:1,data:[]}
           return
         }
         let index = 0
         let txt = ''
         res.data.data.forEach(element=>{
           if(this.linzhengExistItem.indexOf(element.fangJiSubCode) < 0){
             this.linzhengExistItem.push(element.fangJiSubCode)
             this.linzhengTitleList[index] = {id: index + 1 ,data: []}
             this.linzhengTitleList[index].data.push(element)
             txt += element.detailItemName + ' '
             index ++
           }else{
             txt += element.detailItemName + ' '
             this.linzhengTitleList[index-1].data.push(element)
           }
         })
         this.linZhengShowText += txt + ';'
         this.linzhengTitleList = [...this.linzhengTitleList]
       })
    }
  },
  created(){
    if(!this.empBasicMainID){
      this.$router.push('list')
    }
  },
  mounted(){
   this.getTpl()
   this.getFrequency()
   sessionStorage.removeItem('zhzy_gcxt_xybm_name')
   sessionStorage.removeItem('zhzy_gcxt_xybm_code')
  // this.getFangJiCode() // 获取方剂code 停用
  this.getSavedData()
  this.handelGetEmpBasic()
  this.handelGetXYList()
  this.getLZZL()
}
}
</script>
