  line-height: 40px;
  background-color: #FAF7F6;
  color: #3E85FA;
  font-size: 16px;
}
.linzhengzhiliao .middle-bar i{
  cursor: pointer;
}
.linzhengzhiliao .content{
  margin-top: 10px;
  padding: 12px 20px;
  height: 300px;
  overflow: auto;
  background-color: #FAF7F6;
}
</style>




