<template>
  <el-row>
  <el-col :span="8" class="check-tree">
      <el-tree  @node-click="handleNodeClick" ref="tree"
      :data="treeData"
      :default-expand-all="true">
        <span class="custom-tree-node" slot-scope="{ node, data }">
          <span>{{ node.label }}</span>
          <span v-show="data.detailItemCode">
            <el-button @click="handleAdd(data)"
              type="text"
              size="medium">
              ￥{{data.price?data.price:0}}
            </el-button>
            <el-button
              type="text" @click="handleAdd(data)"
              size="medium">
              添加
            </el-button>
          </span>
        </span>
      </el-tree>
  </el-col>
  <el-col :span="15" :offset="1">
      <!-- <el-input placeholder="输入想要查询的项目拼音码"></el-input> -->
      <el-table
        ref="checkTable"
        @selection-change="handleSelectionChange"
        :data="tabelList"
        style="width: 100%; height: 448px; overflow-y:scroll">
        <!-- <el-table-column type="selection" width="55"></el-table-column> -->
        <el-table-column prop="detailItemName" label="项目(已选)" align="center"></el-table-column>
        <el-table-column prop="price" label="单价" align="center"></el-table-column>
        <!-- <el-table-column prop="pinYinCode" label="拼音码"></el-table-column> -->
        <el-table-column label="操作" align="center" width="120">
          <template slot-scope="scope">
            <el-button-group>
                  <span class="icon-button-img" @click="handelDelete(scope.row,scope)" ><img src="../assets/imgs/delete.png"></span>
            </el-button-group>
          </template>
      </el-table-column>
    </el-table>
  </el-col>
  <el-col :span="24" align="center" class="toolbar">
    <el-button type="primary" @click="handlePrint()">打 印</el-button>
    <el-button type="primary" @click="handleSave">保 存</el-button>
  </el-col>
</el-row>
</template>
<script>
import {
  getCheckItemList, // 检查/检验项目
  getCheckItemDetailList, // 检查/检验子项目
  saveCheckItem, // 保存检查项
} from '../api'
import { mapState } from "vuex";
export default {
    computed:{
    ...mapState('patient',{
      registrationNum:state=>state.registrationNum,
      empBasicMainID:state=>state.empBasicMainID,
      // mecidalNum: state => state.mecidalNum,
      // deptCode:state=>state.deptCode
    }),
  },
  watch:{
    getServerList:{
      immediate:true,
      handler:function(curVal,oldVal){
        if(curVal.length>0){
          curVal.forEach(element=>{
            if(element.checkFlag==this.flag){
              this.tabelList.push(element)
            }
          })
        }
     }
    }
  },
  props:{
    flag:{type: Number, default: 0},
    jianceExistArr:{type:Array},
    getServerList: {type:Array}, // 接受远程端数据
  },
  data(){
    return{
      treeData:[
        // {
        //   label: '全部',
        //   children: [],
        // }
      ],
      props1: {
          label: 'name',
          children: 'zones'
      },
      tabelList: [],
      summaryCode: '',
      listData: [],
    }
  },
  methods:{
    handleNodeClick(item,node){ // 单击树 触发更新
      this.treeData.forEach(element=>{
        if(element.id == item.id){
          if(element.children){
            return
          }
          getCheckItemDetailList({summaryCode:item.id}).then(res=>{
            if(res.data.code == '0'){
                res.data.data.forEach(sub=>{
                  sub.label = sub.detailItemName
                  sub.id = sub.detailItemCode
                })
               element.children = res.data.data
            }else{
              element.children = []
            }
              this.treeData = [...this.treeData]
          })
        }
      })
    },
    // 打印检查项
    handlePrint () {
      let routeData = this.$router.resolve({ path: '/print/check', query: {  id: this.registrationNum , flag: this.flag} })
      window.open(routeData.href, '_blank')
    },
    handleAdd(data){ // 添加检查项
      let repeatItem = false
      this.tabelList.forEach(element=>{
        if(element.detailItemCode == data.detailItemCode){
          repeatItem = true
          return
        }
      })
      if(repeatItem){
        this.$message.warning('项目已存在，请勿重复添加!')
        return
      }
      if(data.price == null){
        this.$message.warning('项目价格为空，不能添加!')
        return
      }
      this.tabelList.push(data)
      this.tabelList = [...this.tabelList]
    },
    handelDelete(row,s){ // 移除选中项目
      this.tabelList.splice(s.$index,1)
    },
    handleSave(){ // 保存检查项
      if (this.tabelList.length==0) {
        this.$message.warning('没有选中的项目!')
        // return
      }
      saveCheckItem({
      "empBasicMainID": this.empBasicMainID,
      "checkFlag": this.flag,
      "summaryCode": this.summaryCode,
      "checkItemList": this.tabelList
      }).then(res=>{
        if(res.data.code=='0'){
          this.$message.success('保存成功')
          let temp = '' // 返回父组件显示
          this.tabelList.forEach((element)=> {
            temp += element.detailItemName + ' '
          })
          this.$emit('get-data',{flag: this.flag,content: temp})
        }
      })
    },
    handleSelectionChange(val,row){ // 表单选择
      this.tabelList = val
    }
  },
  mounted(){
      getCheckItemList({
        detailItemFlag: this.flag
      }).then(res=>{
        let data = res.data.data
        data.forEach(element => {
          // this.treeData[0].children.push({id:element.summaryCode,label:element.summaryName})
          this.treeData.push({id:element.summaryCode,label:element.summaryName})
        })
      })

  }
}
</script>
<style scoped>
.check-tree{
  max-height: 520px;
  overflow: scroll;
  overflow-x: hidden;
  background: #faf7f6;
  height: 448px;
}
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 16px;
  }
</style>






